.skillsWrapper {
    position: relative;
    min-height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--second-bg-color);
}

.skillsContainer {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 9%;
    gap: 5rem;
    margin: 9% 0;
}

.skillsRow {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 10rem;
}

.skillsColumn {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
}

.title {
    font-size: 2.5rem;
}

.skillsBox {

}

.skillsContent {
        position: relative;
        border: .5rem solid var(--main-color);
        border-radius: .6rem;
        padding: .5rem 1.5rem;
        overflow: hidden;
        z-index: 1;
}

.skillsContent::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background: var(--bg-color);
    z-index: -1;
    transition: .5s;
}

.skillsContent:hover::before {
    width: 100%;
}

.progress {
    padding: 1rem 0;
}

.progress h3 {
    font-size: 1.7rem;
    display: flex;
    justify-content: space-between;
}

.progress h3 span {
  color: var(--main-color);
}

.bar {
    height: 2.5rem;
    border-radius: .6rem;
    border: .2rem solid var(--main-color);
    padding: .5rem;
    margin: 1rem 0;
}

.bar span {
    display: block;
    height: 100%;
    width: 1%;
    border-radius: .3rem;
    background: var(--main-color);
}

.appear10 {
    animation: appearance10 3s ease;
}

@keyframes appearance10 {
    from {
        width: 1%;
    }
    to {
        width: 10%;
    }
}
